
<script>
import DataService from "../../../services/DataService";
import DynamicForm from "../../../components/Common/DynamicForm.vue";
import modaladdindustry from '../../../components/Common/Modal/modaladdindustry.vue'
import modaladdroletitle from '../../../components/Common/Modal/modaladdroletitle.vue'
import modaladdleadsource from '../../../components/Common/Modal/modaladdleadsource.vue'
export default {
    components: {
        DynamicForm,
        modaladdindustry,
        modaladdroletitle,
        modaladdleadsource
    },
    data() {
        return {
            isLoading:false,
            value: "",
            LeadData: null,
            GroupBy: [],
            dealId: this.$route.params.id,
            CountryData: null,
            showaddindustryModal: false,
            IndustryItemObject: null,
            RoleTitleItemObject: null,
            showaddroletitleModal: false,
            showaddleadsourceModal: false,
            LeadSourceItemObject: null,
            clientData: [],
            cutomerId: 0,
            isAccountSelected: false,
            phoneList: [],
            submited: false,
            userid: null,
        companyId: null,
        username: null,
        dateFormat:null
        };
    },
    mounted: function () { },
    created: function () {
        this.userid = this.GetUserInfo.ID;
    this.companyId = this.GetUserInfo.CompanyId;
    this.username = this.GetUserInfo.Name;
    this.dateFormat=this.GetUserInfo.DateFormat;
        this.FetchData();

    },
    methods: {
        FetchData: function () {
            var vm = this;
            vm.isLoading=true;
            const params = `id=${vm.$route.params.id}&moduleName=CRM&subModuleCode=CRM_DEALS&langCode=en`;
            DataService.getFormLayout(params).then(function (response) {
                vm.isLoading=false;
                if (response.data != "" && response.data != null) {
                    var Compphone = '';
                    var homephone = '';
                    var businessphone = '';
                    var Phones = [];
                    vm.LeadData = response.data.data;
                    if (vm.LeadData != null && vm.LeadData.length > 0) {
                        var groupId = vm.LeadData[0].group_id;
                        vm.LeadData.forEach(item => {
                            groupId = item.group_id;
                            if (item.name == 'industry_type_id' || item.name == "lead_source_id" || item.name == "role_title_id") {
                                item.isPlusIcon = true;
                            }
                            if (item.name == 'customer_id') {
                                vm.cutomerId = item.value == '' ? 0 : item.value;
                            }
                            if (item.dt_code == 'checkbox') {
                                item.select_value = item.value.split(',');
                            }
                            if (item.name == "mobile_phone") {
                                Compphone = item.value;
                                if (Compphone == '' || Compphone == null || Compphone.indexOf("+") == -1)
                                    Phones.push(item.name);
                            }
                            if (item.name == "home_phone") {
                                homephone = item.value;
                                if (homephone == '' || homephone == null || homephone.indexOf("+") == -1)
                                    Phones.push(item.name);
                            }
                            if (item.name == "business_phone") {
                                businessphone = item.value;
                                if (businessphone == '' || businessphone == null || businessphone.indexOf("+") == -1)
                                    Phones.push(item.name);
                            }
                            if (item.name == 'lead_source_id' && vm.dealId > 0) {
                                if (item.value != '') {
                                    setTimeout(function () {
                                        vm.onchangechannel(item, null);
                                    }, 500)
                                }
                            }
                            if (item.name == 'deal_owner_id') {
                                    if (item.value == '') {
                                        item.select_value = vm.username;
                                        item.value ={name: item.select_value, value:  vm.userid, first_value: null, second_value: null};
                                        item.select_options=[{name: item.select_value, value:  vm.userid, first_value: null, second_value: null}]
                                    }
                                    else {
                                       item.select_options =vm.MakeNormalArray(item.select_options);
                                    }
                                };
                            var chkGroup = $.grep(vm.GroupBy, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(vm.LeadData, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (vm.dealId > 0) {
                                    $.each(returnedData, function (filedIndex, fieldItem) {                                       
                                        if (fieldItem.dropdown_type == "custom" && fieldItem.picklist_options == "Lookup") {
                                            fieldItem.select_options = vm.MakeNormalArray(fieldItem.select_options);
                                            fieldItem.select_value = fieldItem.select_options;
                                            if (fieldItem.select_options != null && fieldItem.select_options.length > 0) {
                                                fieldItem.value = fieldItem.select_options[0];
                                            }
                                        }
                                        else if (fieldItem.dt_code == "decimal" && fieldItem.form_field_visibility == "YES" && fieldItem.value > 0) {
                                            fieldItem.value = parseFloat(fieldItem.value, 10).toFixed(2);
                                        }
                                        else if (fieldItem.dt_code == "date") {
                                            fieldItem.value = fieldItem.value;                                                                               
                                        }
                                    });
                                }
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        group_display_name: returnedData[0].group_display_name,
                                        Data: returnedData
                                    };
                                    vm.GroupBy.push(objGroup);
                                }
                            }
                        })
                        if (vm.dealId > 0) {
                            setTimeout(function () {
                                var selectedName = $('select[name="lead_source_id"] option:selected').text();
                                if (selectedName == "Partner" || selectedName == "Consultant" || selectedName == "Provider" || selectedName == "Customer") {
                                    $(".campaignname").hide();
                                    $.each(items, function (indx, child) {
                                        if (child.name == "referred_by")
                                            child.value = "";
                                        if (child.name == "campaign_id")
                                            child.value = "";
                                    });
                                }
                                else if (selectedName == "Campaign") {
                                    $(".campaignname").show();
                                    $(".referredby").hide();
                                }
                                else {
                                    $(".campaignname").hide();
                                    $(".referredby").hide();
                                    $.each(vm.GroupBy, function (indx, child) {
                                        $.each(child.Data, function (ind, child1) {
                                            if (child1.name == "referred_by")
                                                child1.value = "";
                                            if (child1.name == "campaign_id")
                                                child1.value = "";
                                        });
                                    });
                                }
                            }, 500);
                        }
                        else {
                            setTimeout(function () {
                                $(".referredby").hide();
                                $(".campaignname").hide();
                            }, 500);
                        }
                        if(vm.dealId>0){
                            var frmFields={}
                        vm.LeadData.forEach((item)=>{
                             if(item.astype!='' && item.form_field_visibility == 'YES'){                               
                             frmFields[item.name]=item.value;
                             }
                         })                         
                        //  vm.$refs.form.resetForm({values:frmFields});
                        }
                    }
                }
            });
        },
      
        MakeNormalArray: function (value) {
            if (value) {
                try {
                    return JSON.parse(value);
                }
                catch (ex) {
                    return value;
                }
            }
            else {
                value = [];
            }
        },
        FillDropdown: function (item, action) {

            var self = this;
            var refId = '';
            var typeCode = '';
            if (item.name == 'deal_country') {
                typeCode = 'ACCOUNT_STATE';
                if (action == null) {
                    $.each(self.GroupBy, function (index, item) {
                        $.each(item.Data, function (index, element) {
                            if (element.name == ' deal_city' || element.name == "deal_state" || element.name == 'deal_zipcode' || element.name == 'deal_address1' || element.name == '  deal_address2') {
                                element.select_options = null;
                                element.value = "";
                            }
                        });
                    });
                }
            }
            else if (item.name == 'account_id') {
                typeCode = "COMMISSION_AGREEMENT";
                action = 'Account';
            }
            var itemname = item.name;
            refId = item.value;
            if (refId != '') {
                var params = `id=&moduleName=${self.$route.params.moduleName}&type=${typeCode}&refId=${refId}`;
                DataService.getDDLData(params).then(response => {
                    var json = [];
                    if (response.data != null) {
                        json = response.data.DATA;
                    }
                    $.each(self.GroupBy, function (index, item) {
                        $.each(item.Data, function (index, element) {
                            if (element.name == 'deal_state' && itemname == 'deal_country') {
                                element.select_options = json;
                            }
                            else if (element.name == 'deal_city' && itemname == 'deal_state') {
                                element.select_options = json;
                            }
                            else if (element.name == 'commission_type' && action == 'Account')
                                element.value = json[0].second_value;
                            else if (element.name == 'agreement_type' && action == 'Account')
                                element.value = json[0].first_value;
                        });
                    });
                    item.name = itemname;
                     
                });
            }
        },
        onchangechannel: function (item, items) {
            var refId = '';
            var typeCode = '';
            var vm = this;
            if (item.name == "lead_source_id") {
                var selectedName = $('select[name="lead_source_id"] option:selected').text();
                if (selectedName == "Partner" || selectedName == "Consultant" || selectedName == "Customer" || selectedName == "Provider") {
                    $(".campaignname").hide();
                    $(".referredby").show();
                    $.each(items, function (indx, child) {
                        if (child.name == "referred_by")
                            child.value = "";
                        if (child.name == "campaign_id")
                            child.value = "";
                    });
                }
                else if (selectedName == "Campaign") {
                    $(".referredby").hide();
                    $(".campaignname").show();
                    $.each(items, function (indx, child) {
                        if (child.name == "referred_by")
                            child.value = "";
                    });
                }
                else {
                    $.each(items, function (indx, child) {
                        if (child.name == "referred_by")
                            child.value = "";
                        if (child.name == "campaign_id")
                            child.value = "";
                    });
                    $(".referred_by").hide();
                    $(".campaignname").hide();
                }
                var typename = $('select[name="lead_source_id"] option:selected').text();
                var params = `id=&moduleName=${vm.$route.params.moduleName}&type=ACCOUNTS_LIST&search=${typename}`;
                DataService.getDDLData(params).then(function (response) {
                    var json = [];
                    if (response.data != null) {
                        json = response.data.DATA;
                    }
                    $.each(vm.GroupBy, function (index, item) {
                        $.each(item.LeadData, function (index, element) {
                            if (element.name == 'referred_by') {

                                element.select_options = json;
                            }
                        });
                    });
                })
            }
            else if (item.name == "account_id") {
                typeCode = 'ACCOUNT_CONTACTS';
                refId = item.value;
                if (refId != '') {
                    var params = `id=&moduleName=${vm.$route.params.moduleName}&type=${typeCode}&refId=${refId}`;
                    DataService.getDDLData(params).then(function (response) {
                        var json = [];
                        if (response.data != null) {
                            json = response.data.DATA;
                        }
                        $.each(vm.GroupBy, function (index, item) {
                            $.each(item.Data, function (index, element) {
                                if (element.name == 'contact_name') {

                                    element.select_options = json;
                                }
                            });
                        });
                    })
                }

            }
            else if (item.name == 'deal_country') {
                typeCode = 'STATE';
                refId = item.value;
                if (refId != '') {
                     
                    var param = `id=&moduleName=${vm.$route.params.moduleName}&type=${typeCode}&refId=${refId}`;
                    DataService.getDDLData(param).then(response => {
                        var json = [];
                        if (response.data != null) {
                            json = response.data.DATA;
                        }
                        $.each(vm.GroupBy, function (index, item) {
                            $.each(item.Data, function (index, element) {
                                if (element.name == 'deal_state') {

                                    element.select_options = json;
                                }
                            });
                        });
                         
                    });
                }
            }

             
        },
        onSearch: function onSearch(search, objItem) {
            if(search)     {
            this.searchSelectData(search, this, objItem);
            }
        },
        searchSelectData: function (search, vm, objItem) {
            this.ShowSearchData(search, vm, objItem,objItem.value.value);
        },
        ShowSearchData(search, vm, objItem,selctedValue) {
            if (typeof (selctedValue) == 'undefined') {
                    selctedValue = 0;
                }
            var url = "id=&moduleName="+this.$route.params.moduleName+"&type="+objItem.field_code;
            if (objItem.dependent_type == 'fill') {
                    url += "&refId=" + selctedValue;
                }
                DataService.getDDLData(url + "&search=" + escape(search))
                .then(response => {
                    var json = [];
                    if(response.data==''){
                       
                    }
                    if (response.data != '') {
                        json = response.data;
                    }                    
                   
                    objItem.select_options = json.DATA;
                        return objItem.select_options;
                    
                });
        },
        setSelectedValue(item, val) {
                var vm = this;
                // vm.$refs.form.setFieldTouched(item.name, false); 
                try {
                    if (Array.isArray(val)) {
                        item.value = (typeof val[0] === 'object' && val[0] !== null) ? val[0].value : val[0];
                    }
                    else {
                     
                        if (item.has_dependent >= 0) {
                            vm.GroupBy.forEach((group)=>{                               
                                group.Data.forEach((childItem)=>{
                                    if (childItem.parent_id == item.custom_field_id && childItem.dependent_type == 'fill') {
                                        vm.ShowSearchData('', vm, childItem, val.value);
                                    }
                                })
                            })
                            
                        }
                        item.value = val;
                    }
                    if (item.name == 'account_id') {
                        vm.getclientAssociatedWithAccount(item.value);
                        //vm.FillDropdown('ACCOUNT_CONTACTS', item.value);
                    }
                }
                catch (ex) {
                }
            },            
        onSubmit: function (valid,errors) {
            debugger;
            var vm = this;
            // this.LeadData.forEach((item) => {
            //     if (item.astype != '' && item.form_field_visibility == 'YES') {
            //         if (item.dt_code == "select" && item.value) {
            //             try {
            //                 values[item.name] = String((typeof item.value === 'object' && item.value !== null) ? item.value.value : item.value);
            //                 //vm.$refs.form.setFieldTouched(item.name, false); 
            //             } catch (ex) { console.log('error while get dropdown parse data') }
            //         }
            //         if (item.dt_code == 'date' && item.value) {
            //             values[item.name] =vm.$options.filters.formatDate(item.value);
            //             //vm.$refs.form.setFieldTouched(item.name, false);
            //         } else {
            //            // vm.$refs.form.setFieldTouched(item.name, true);
            //         }
            //     } 
            // })
            this.$refs.form.validate().then((result) => {               
                if (result) {
                    vm.isLoading=true;
                    var fullJson = vm.LeadData;
                    var data = [];
                    var validation = [];
                    var selectedRadioValue = $("input[name='radio_clients']:checked").val();
                    var selectedClient = $.grep(vm.clientData, function (element, index) {
                        return element.user_id == selectedRadioValue;
                    });
                    $.each(fullJson, function (key, item) {
                        var validationObject = {
                            field_name: item.name,
                            required: item.is_required,
                            length: item.length,
                            type: item.validation_type
                        };
                        var fieldValue = (item.name == "deal_id" ? vm.dealId : ((typeof item.value === "undefined") ? '' : item.value));
                        fieldValue = typeof fieldValue === "undefined" ? '' : fieldValue;
                        if (item.dt_code == "date") {
                            fieldValue = item.value;
                        }
                        if (item.dt_code == "checkbox") {
                            var filtered = item.select_value.filter(function (el) {
                                return (el != null && el != "");
                            });
                            fieldValue = filtered.join(',');
                        }
                        if (item.dt_code == "select") {
                            try {
                                fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue
                            } catch (ex) { }
                        }                       

                        if (item.name == 'created_by') {
                            fieldValue = vm.userid;
                        }
                        if (item.name == 'modified_by') {
                            fieldValue = vm.userid;
                        }
                        if (item.name == 'company_id') {
                            fieldValue = vm.companyId;
                        }
                                           
                        var dataObject = {
                            field_name: item.name,
                            field_value: fieldValue,
                            custom_field_id: item.custom_field_id
                        };
                        data.push(dataObject);
                        validation.push(validationObject);
                    });                
                    var postJSON = {
                        data: data,
                        validation: validation,
                        clientData: selectedClient
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    console.log(objectPostString);
                    DataService.PostDeal(objectPostString).then(
                        function (response) {
                            vm.isLoading=false;
                            if (response.data[0].Code == "1") {
                                if (vm.dealId > 0) {
                                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Deal')]), "success", true, vm.$t("Alert"));
                                }
                                else {
                                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Deal')]), "success", true, vm.$t("Alert"));
                                }
                                vm.onCancel();
                            }
                            else {
                                vm.ShowAlert(vm.$t('AddedError', [vm.$t('Deal')]), "failure", true, vm.$t("Alert"));
                            }                             
                            vm.submited = false;
                        })
                }
            });
        },
        onCancel: function () {
            this.$router.push({ name: 'CRMDeal' });
        },
        UpdateSelectJson: function (name, json) {
            $.each(this.GroupBy, function (index, item) {
                $.each(item.Data, function (index, element) {
                    if (element.name == name) {
                        element.select_options = json;
                    }
                });
            });
        },
        openaddIndustrypopup: function (item) {        
            this.IndustryItemObject = item;
            this.showaddindustryModal = true;
        },
        openaddRoleTitlepopup: function (item) {
            this.RoleTitleItemObject = item;
            this.showaddroletitleModal = true;
        },
        openaddLeadSourcepopup: function (item) {
            this.LeadSourceItemObject = item;
            this.showaddleadsourceModal = true;
        },
        onBlur(item){
                try {
                    if(String(item.value).length>1){
                    item.value = Number.parseFloat(item.value).toFixed(2);
                    this.$refs.form.setFieldValue(item.name, item.value);
                    }
                } catch (ex) {

                }
            }
    },
};
</script>

<template>
      <div>
        <loader :is-visible="isLoading"></loader>
       
       <section class="main-content">
           <div id="divMaindealData" v-cloak>

          <div class="row">
                   <div class="col-md-12 padding-t_8">
                       <div class="theme-primary partition-full">
                           <span class="p-name text-white">Manage deal</span>
                           <span class="float-right">
                               <a href="javascript:;" class="p-action-btn text-white" title="PageTour" @click="setTourGuideSteps(0)">
                                   <em class="fa fa-road" aria-hidden="true"></em>
                                   <br />PageTour
                               </a>
                           </span>
                       </div>
                   </div>
               </div>


               <div class="border p-3">
                   <Form v-slot="{ valid,errors}" ref="form">
                       <DynamicForm :schema="GroupBy" v-on:openaddIndustrypopup="openaddIndustrypopup"
                           :dateformat="GetUserInfo.DateFormat" v-on:openaddRoleTitlepopup="openaddRoleTitlepopup"
                           v-on:openaddLeadSourcepopup="openaddLeadSourcepopup" v-on:onchangechannel="onchangechannel" 
                           v-on:onSearchDDL="onSearch"  v-on:setSelectedValue="setSelectedValue" v-on:onBlur="onBlur"/>
                       <div class="row flex-row-reverse mt-2">
                           <div class="col-lg-6 text-right">
                               <a href="javascript:;" @click="onSubmit(valid,errors)"
                                  class="btn btn-success AddDealDetail_a" title="Save">
                                   <em class="fa fa-save pr-2"></em>Save
                               </a>&nbsp;
                               <a href="javascript:void(0)" class="btn btn-danger" title="Cancel" @click="onCancel">
                                   <em class="fa fa-close pr-2"></em>Cancel
                               </a>
                           </div>
                           <div class="col-lg-6">
                               <div class="py-2">
<small class="text-danger">{{$t('MandatoryString')}}</small>
                               </div>
                           </div>
                       </div>
                       <modaladdindustry v-if="showaddindustryModal" @close="showaddindustryModal=false"
                           @updatedata="UpdateSelectJson"></modaladdindustry>
                       <modaladdroletitle v-if="showaddroletitleModal" @close="showaddroletitleModal=false"
                           @updatedata="UpdateSelectJson"></modaladdroletitle>
                       <modaladdleadsource v-if="showaddleadsourceModal" callFrom="deal"
                           @close="showaddleadsourceModal=false" @updatedata="UpdateSelectJson"></modaladdleadsource>

                   </Form>
               </div>
           </div>
       </section>
      </div>
        
    
</template>
